.borderLeft {
    border-left: 3px solid black;
}

.borderTop {
    border-top: 3px solid black;
}

.Square {
    width: 10vw;
    height: 10vw;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
    overflow: hidden;
    background-color: white;
    border-radius: 50%;
}